import React from 'react';

import {
  Panel,
  PanelHeader,
  Search,
  PanelSpinner,
  Div,
  Group,
  Header,
  List,
  Cell,
  Link,
  FixedLayout,
  Button
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import {smoothScrollToTop} from '../services/functions';

import bridge from '@vkontakte/vk-bridge';

import Icon28ArrowUpOutline from '@vkontakte/icons/dist/28/arrow_up_outline';

export default class GetChairsPanel extends React.Component {
  constructor (props) {
    super(props);
    
    let {hashParams} = props;
    let method = hashParams.get('method');
    this.isEducationMethod = ['selectFaculty', 'selectUniversity', 'selectChair', 'selectSchool', 'selectMetroStation'].indexOf(method) !== -1;

    this.state = {
      countries: [],
      cities: [],
      unversities: [],
      faculties: [],
      chairs: [],
      schools: [],
      regions: [],
      metroStations: [],

      selectedCountryId: 0,
      selectedCityId: 0,
      selectedFacultyId: 0,
      selectedUniversityId: 0,
      selectedChairId: 0,
      selectedSchoolId: 0,
      selectedRegionId: 0,
      selectedMetroStationId: 0,

      loadedCountries: false,
      loadedCities: false,
      loadedUniversities: false,
      loadedFaculties: false,
      loadedChairs: false,
      loadedSchools: false,
      loadedRegions: false,
      loadedMetroStations: false,

      countryName: '',
      cityName: '',
      universityName: '',
      facultyName: '',
      chairName: '',
      schoolName: '',
      regionName: '',
      metroStationName: '',

      searchQuery: '',

      page: 'countries',

      showScrollToTop: false
    }

    this.scrolls = {
      'countries': 0,
      'cities': 0,
      'regions': 0,
      'unversities': 0,
      'faculties': 0,
      'chairs': 0,
      'metro_stations': 0,
      'schools': 0
    }

    this.backActions = [];

  }

  componentDidMount () {
    let isEducationMethod = this.isEducationMethod
    let {hashParams, activeHash} = this.props;

    if (hashParams.get('country_id')) {
      if (activeHash === "selectRegion") {
        this.goToRegions()
      } else {
        this.goToCities()
      }
    } else if (hashParams.get('city_id') && isEducationMethod) {
      if (activeHash === "selectSchool") {
        this.goToSchools()
      } else if (activeHash === "selectMetroStation") {
        this.goToMetroStations()
      } else {
        this.goToUniversities()
      }
    } else if (hashParams.get('university_id') && isEducationMethod) {
      this.goToFaculties() 
    } else if (hashParams.get('faculty_id') && isEducationMethod) {
      this.goToChairs() 
    } else if (!this.state.countries.length) {
      this.loadCountries();
    }

    window.onpopstate = (e) => {
      console.log(e)
      if (e.type === "popstate") {
        console.log(this.backActions)
        if (this.backActions.length && this.backActions[this.backActions.length - 1]) {
          try {
            this.backActions[this.backActions.length - 1]();
          } catch (e) {
            console.error(e)
          }
          
          this.backActions = this.backActions.slice(0, this.backActions.length - 1);

        }
        // window.history.pushState(null, null)
      }
    }

    window.addEventListener("scroll", this.saveScrolls)
  }

  componentWillUnmount () {
    window.removeEventListener("scroll", this.saveScrolls)
  }

  componentDidUpdate () {
    if (window.scrolling) return;
    window.scrollTo(0, this.scrolls[this.state.page]);
  }

  saveScrolls = (e) => {
    this.scrolls[this.state.page] = window.pageYOffset;

    if (window.pageYOffset > 320 && !this.state.showScrollToTop) {
      this.setState({ 
        showScrollToTop: true
      })
    } else if (this.state.showScrollToTop && window.pageYOffset <= 320) {
      this.setState({
        showScrollToTop: false
      })
    }
  }

  async goToCities () {
    let {hashParams, vkApi} = this.props;
    
    let country = await  vkApi('database.getCountriesById', {
      country_ids: Number(hashParams.get('country_id'))
    });

    country = country[0];

    this.setState({
      selectedCountryId: country.id,
      countryName: country.title,
      loadedCountries: true,
      page: 'cities'
    }, () => {
      this.loadCities();
    })
  }

   async goToRegions () {
    let {hashParams, vkApi} = this.props;
    
    let country = await  vkApi('database.getCountriesById', {
      country_ids: Number(hashParams.get('country_id'))
    });

    country = country[0];

    this.setState({
      selectedCountryId: country.id,
      countryName: country.title,
      loadedCountries: true
    }, () => {
      this.loadRegions();
    })
  }

  async goToUniversities () {
    let {hashParams, vkApi} = this.props;
    
    let city = await  vkApi('database.getCitiesById', {
      city_ids: Number(hashParams.get('city_id'))
    });

    city = city[0];

    this.setState({
      selectedCityId: city.id,
      cityName: city.title,
      loadedCities: true
    }, () => {
      this.loadUniversities();
    })
  }

  async goToSchools () {
    let {hashParams, vkApi} = this.props;
    
    let city = await  vkApi('database.getCitiesById', {
      city_ids: Number(hashParams.get('city_id'))
    });

    city = city[0];

    this.setState({
      selectedCityId: city.id,
      cityName: city.title,
      loadedCities: true
    }, () => {
      this.loadSchools();
    })
  }

  async goToMetroStations () {
    let {hashParams, vkApi} = this.props;
    
    let city = await  vkApi('database.getCitiesById', {
      city_ids: Number(hashParams.get('city_id'))
    });

    city = city[0];

    this.setState({
      selectedCityId: city.id,
      cityName: city.title,
      loadedCities: true
    }, () => {
      this.loadMetroStations();
    })
  }

  async goToFaculties () {
    let {hashParams, vkApi} = this.props;
  
    this.setState({
      selectedUniversityId: Number(hashParams.get('university_id')),
      loadedUniversities: true
    }, () => {
      this.loadFaculties();
    })
  }

  async goToChairs () {
    let {hashParams, vkApi} = this.props;
  
    this.setState({
      selectedFacultyId: Number(hashParams.get('faculty_id')),
      loadedFaculties: true
    }, () => {
      this.loadChairs();
    })
  }

  async loadCountries (q="") {
    
    this.setState({
      loadedCountries: false
    });

    let countries = await this.props.vkApi('database.getCountries', {
      need_all: 1,
      q,
      count: 1000
    });

    this.setState({
      loadedCountries: true,
      countries: countries.items
    })
  }

  async loadCities (q="") {
    this.setState({
      loadedCities: false
    });

    let cities = await this.props.vkApi('database.getCities', {
      country_id: this.state.selectedCountryId,
      q
    })

    this.setState({
      loadedCities: true,
      cities: cities.items
    })
  }

  async loadRegions (q="") {
    this.setState({
      loadedRegions: false
    });

    let regions = await this.props.vkApi('database.getRegions', {
      country_id: this.state.selectedCountryId,
      q
    })

    this.setState({
      loadedRegions: true,
      regions: regions.items,
      page: 'regions'
    })
  }

  selectCountry = (countryId, title) => {
    const {activeHash} = this.props;
    if (activeHash === "selectCountry") {
      this.props.sendResponse({
        "country_id": countryId,
        "country_title": title
      });
    } else {
      this.nextPage();
      
      this.backActions.push(activeHash === "selectRegion" ? this.clearRegions : this.clearCities);

      this.setState({
        selectedCountryId: countryId,
        searchQuery: '',
        countryName: title,
        page: 'cities'
      }, () => {
        if (activeHash === "selectRegion") {
          this.loadRegions();
        } else {
          this.loadCities();
        }
      });
    } 
  }

  nextPage () {
    window.history.pushState(null, null)
  }

  goBack = () => {
    console.log('back')
    window.history.back();
  }

  selectCity = async (city) => {
    const {activeHash} = this.props;
    
    if (activeHash === "selectCity") {
      this.props.sendResponse({
        "country_id": this.state.selectedCountryId,
        "country_title": this.state.countryName,
        "city_id": city.id,
        "city_title": city.title
      });
    } else {
      this.nextPage();
      
      this.backActions.push(activeHash === "selectSchool" ? this.clearSchool : 
      activeHash === "selectMetroStation" ? this.clearMetroStation : this.clearUniversity);

      this.setState({
        selectedCityId: city.id,
        searchQuery: '',
        cityName: city.title,
        page: activeHash === "selectSchool" ? "schools" : activeHash === "selectMetroStation" ? "metro_stations" : "universities"
      }, () => {
        if (activeHash === "selectSchool") {
          this.loadSchools()
        } else if (activeHash === "selectMetroStation") {
          this.loadMetroStations()
        } else {
          this.loadUniversities()
        }
      })
    }
  }

  selectRegion = async (region) => {
    const {activeHash} = this.props;
    
    this.props.sendResponse({
      "country_id": this.state.selectedCountryId,
      "country_title": this.state.countryName,
      "region_id": region.id,
      "region_title": region.title
    });
  }

  selectChair = async (chair) => {
    this.props.sendResponse({
      "chair_id": chair.id,
      "chair_title": chair.title,
      
      "city_id": this.state.selectedCityId || 0,
      "city_title": this.state.cityName || "",

      "country_id": this.state.selectedCountryId || 0,
      "country_title": this.state.countryName || "",

      "university_id": this.state.selectedUniversityId || 0,
      "university_title": this.state.universityName || "",


      "faculty_id": this.state.selectedFacultyId || 0,
      "faculty_title": this.state.facultyName || ""
    });
  }

  async loadUniversities (q="") {
    this.setState({
      loadedUniversities: false
    });

    let unversities = await this.props.vkApi('database.getUniversities', {
      city_id: this.state.selectedCityId,
      q
    })

    this.setState({
      loadedUniversities: true,
      unversities: unversities.items,
      page: 'universities'
    })
  }

  async loadSchools (q="") {
    this.setState({
      loadedSchools: false
    });

    let schools = await this.props.vkApi('database.getSchools', {
      city_id: this.state.selectedCityId,
      q
    })

    this.setState({
      loadedSchools: true,
      schools: schools.items,
      page: 'schools'
    })
  }

  async loadMetroStations () {
    this.setState({
      loadedMetroStations: false
    });

    let stations = await this.props.vkApi('database.getMetroStations', {
      city_id: this.state.selectedCityId,
      count: 500
    })

    this.setState({
      loadedMetroStations: true,
      metroStations: stations.items,
      page: 'metro_stations'
    })
  }

  clearCities = () => {
    let newState = {
      cities: [],
      selectedCountryId: 0,
      countryName: '',
      searchQuery: '',
      page: 'countries'
    }

    const {hashParams} = this.props;

    if (hashParams.get('country_id') && !this.state.countries.length) {
      this.loadCountries();
    }

    this.scrolls['cities'] = 0;
    this.setState(newState)
  }

  clearRegions = () => {
    let newState = {
      regions: [],
      selectedCountryId: 0,
      countryName: '',
      searchQuery: ''
    }

    const {hashParams} = this.props;

    if (hashParams.get('country_id') && !this.state.countries.length) {
      this.loadCountries();
    }
    
    this.scrolls['regions'] = 0;

    this.setState(newState)
  }

  renderCities () {
    const {cities, loadedCities, searchQuery} = this.state;
    
    const {hashParams} = this.props;

    let canChangeCountry = hashParams.get('can_change_country') !== 'no';

    return (
      <Group header={<Header mode="secondary" aside={canChangeCountry ? <Button className="btn-15" mode="tertiary" onClick={this.goBack}> </Button> : null}>  {this.getTargetName()}</Header>}>
        <List>
          {cities.map(city => (
            <Cell onClick={() => this.selectCity(city)} key={"city" + city.id}>
              {city.title}
            </Cell>
          ))}
          {!cities.length ? <Div>  </Div> : null}
        </List>
      </Group>
    )

  }

  renderRegions () {
    const {regions, searchQuery} = this.state;
    
    const {hashParams} = this.props;

    let canChangeCountry = hashParams.get('can_change_country') !== 'no';

    return (
      <Group header={<Header mode="secondary" aside={canChangeCountry ? <Button className="btn-15" mode="tertiary" onClick={this.goBack}> </Button> : null}> </Header>}>
        <List>
          {regions.map(region => (
            <Cell onClick={() => this.selectRegion(region)} key={"region" + region.id}>
              {region.title}
            </Cell>
          ))}
          {!regions.length ? <Div>  </Div> : null}
        </List>
      </Group>
    )

  }

  changeSearchQuery = (e) => {
    let q = e.target.value;
    const {activeHash} = this.props;

    clearTimeout(this.searchTimeout);
    this.searchTimeout = setTimeout(() => {
    
      this.scrolls[this.state.page] = 0;
      this.setState({
        searchQuery: String(q).toLocaleLowerCase()
      });

      if (this.state.selectedCountryId && !this.state.selectedCityId && !this.state.selectedUniversityId) {
        if (activeHash === "selectRegion") {
          this.loadRegions(q);
        } else {
          this.loadCities(q);
        }
      } else if (this.state.selectedCityId && !this.state.selectedUniversityId && !this.state.selectedSchoolId) {
        if (activeHash === "selectSchool") {
          this.loadSchools(q);
        } else {
          if (activeHash !== "selectMetroStation") {
             this.loadUniversities(q);
          }
        }
      }
    }, 400)
  }

  getTargetName () {
    const {activeHash} = this.props;
    if (this.isEducationMethod) {
      if (activeHash === "selectSchool") {
        return '';
      } else if (activeHash === "selectMetroStation") {
        return ''
      } else {
        return '';
      }
    }

    return '';
  }
  renderCountries () {
    const {countries, searchQuery} = this.state;
    return (
      <Group header={<Header mode="secondary">  {this.getTargetName()}</Header>}>
        <List>
          {countries.filter(c => {
            return String(c.title).toLocaleLowerCase().match(searchQuery)
          }).map(country => (
            <Cell onClick={() => this.selectCountry(country.id, country.title)} key={"country" + country.id}>
              {country.title}
            </Cell>
          ))}
          {!(countries.filter(c => {
              return String(c.title).toLocaleLowerCase().match(searchQuery)
            }).length) ? <Div>  </Div> : null}
        </List>
      </Group>
    );
  }

  async loadFaculties (q="") {
    this.setState({
      loadedFaculties: false
    });

    let faculties = await this.props.vkApi('database.getFaculties', {
      university_id: this.state.selectedUniversityId,
      q,
      count: 10000
    })

    this.setState({
      loadedFaculties: true,
      faculties: faculties.items,
      page: 'faculties'
    })
  }

  async loadChairs () {
    this.setState({
      loadedChairs: false
    });

    let chairs = await this.props.vkApi('database.getChairs', {
      faculty_id: this.state.selectedFacultyId,
      count: 10000
    })

    this.setState({
      loadedChairs: true,
      chairs: chairs.items,
      page: 'chairs'
    })
  }

  clearUniversity = () => {
    const {hashParams} = this.props;

    let newState = {
      selectedCityId: 0,
      unversities: [],
      loadedUniversities: false,
      selectedUniversityId: 0,
      searchQuery: '',
      page: 'cities'
    }
    
    this.scrolls['universities'] = 0;

    this.setState(newState, () => {
      if (hashParams.get('city_id')) {
        this.loadCountries()
      }
    })
  }

  clearMetroStation = () => {
    const {hashParams} = this.props;

    let newState = {
      selectedCityId: 0,
      unversities: [],
      loadedUniversities: false,
      selectedUniversityId: 0,
      searchQuery: '',
      page: 'cities',
      loadedMetroStations: false,
      selectedMetroStationId: 0,
      metroStations: []
    }
    
    this.scrolls['metro_stations'] = 0;

    this.setState(newState, () => {
      if (hashParams.get('city_id')) {
        this.loadCountries()
      }
    })
  }

  clearSchool = () => {
    const {hashParams} = this.props;

    let newState = {
      selectedCityId: 0,
      schools: [],
      loadedSchools: false,
      selectedSchoolId: 0,
      searchQuery: ''
    }
    
    this.scrolls['schools'] = 0;

    this.setState(newState, () => {
      if (hashParams.get('city_id')) {
        this.loadCountries()
      }
    })
  }

  clearFaculty = () => {
    const {hashParams} = this.props;

    let newState = {
      selectedUniversityId: 0,
      faculties: [],
      loadedFaculties: false,
      searchQuery: '',
      page: 'universities'
    }

    this.scrolls['faculties'] = 0;

    this.setState(newState, () => {
      if (hashParams.get('university_id')) {
        this.loadCountries()
      }
    })
  }

  clearChair = () => {
    const {hashParams} = this.props;

    let newState = {
      selectedFacultyId: 0,
      chairs: [],
      loadedChairs: false,
      searchQuery: '',
      page: 'faculties'
    }
    
    this.scrolls['chairs'] = 0;

    this.setState(newState, () => {
      if (hashParams.get('faculty_id')) {
        this.loadCountries()
      }
    })
  }

  selectUniversity = (universityId, title) => {
    const {activeHash} = this.props;
    if (activeHash === "selectUniversity") {
      this.props.sendResponse({
        "country_id": this.state.selectedCountryId,
        "country_title": this.state.countryName,
        "city_id": this.state.selectedCityId,
        "city_title": this.state.cityName,
        "university_id": universityId,
        "university_title": title
      });
    } else {
      this.nextPage();
      this.backActions.push(this.clearFaculty)
      this.setState({
        selectedUniversityId: universityId,
        searchQuery: '',
        universityName: title,
        page: 'faculties'
      }, () => {
        this.loadFaculties();
      })
    }
  }

  selectSchool = (schoolId, title) => {
    const {activeHash} = this.props;
    this.props.sendResponse({
      "country_id": this.state.selectedCountryId,
      "country_title": this.state.countryName,
      "city_id": this.state.selectedCityId,
      "city_title": this.state.cityName,
      "school_id": schoolId,
      "school_title": title
    });
  }

  selectMetroStation = (stationId, title) => {
    const {activeHash} = this.props;
    this.props.sendResponse({
      "country_id": this.state.selectedCountryId,
      "country_title": this.state.countryName,
      "city_id": this.state.selectedCityId,
      "city_title": this.state.cityName,
      "station_id": stationId,
      "station_title": title
    });
  }

  selectFaculty = (facultyId, title) => {
    const {activeHash} = this.props;
    if (activeHash === "selectFaculty") {
      this.props.sendResponse({
        "country_id": this.state.selectedCountryId,
        "country_title": this.state.countryName,
        "city_id": this.state.selectedCityId,
        "city_title": this.state.cityName,
        "university_id": this.state.selectedUniversityId,
        "university_title": this.state.universityName,
        "faculty_id": facultyId,
        "faculty_title": title
      });
    } else {
      this.nextPage();
      this.backActions.push(this.clearChair)
      this.setState({
        selectedFacultyId: facultyId,
        searchQuery: '',
        facultyName: title,
        page: 'chairs'
      }, () => {
        this.loadChairs();
      })
    }
  }

  renderUniversities () {
    const {unversities, searchQuery} = this.state;
    const {hashParams} = this.props;
    return (
      <Group header={<Header mode="secondary" aside={hashParams.get('can_change_city') != 'no' ? <Button className="btn-15" mode="tertiary" onClick={this.goBack}> </Button> : null}> </Header>}>
        <List>
          {unversities.filter(c => {
            return String(c.title).toLocaleLowerCase().match(searchQuery)
          }).map(university => (
            <Cell onClick={() => this.selectUniversity(university.id, university.title)} key={"unversity" + university.id}>
              {university.title}
            </Cell>
          ))}

          {!(unversities.filter(c => {
            return String(c.title).toLocaleLowerCase().match(searchQuery)
          }).length) ? <Div>  </Div> : null}

        </List>
      </Group>
    );
  }

  renderSchools () {
    const {schools, searchQuery} = this.state;
    const {hashParams} = this.props;
    return (
      <Group header={<Header mode="secondary" aside={hashParams.get('can_change_city') != 'no' ? <Button className="btn-15" mode="tertiary" onClick={this.clearSchool}> </Button> : null}> </Header>}>
        <List>
          {schools.map(school => (
            <Cell onClick={() => this.selectSchool(school.id, school.title)} key={"school" + school.id}>
              {school.title}
            </Cell>
          ))}

          {!schools.length ? <Div>  </Div> : null}

        </List>
      </Group>
    );
  }

  renderMetroStations () {
    const {metroStations, searchQuery} = this.state;
    const {hashParams} = this.props;

    return (
      <Group header={<Header mode="secondary" aside={hashParams.get('can_change_city') != 'no' ? <Button className="btn-15" mode="tertiary" onClick={this.goBack}> </Button> : null}> </Header>}>
        <List>
          {metroStations.filter(c => {
            return String(c.name).toLocaleLowerCase().match(searchQuery)
          }).map(station => (
            <Cell onClick={() => this.selectMetroStation(station.id, station.name)} key={"station" + station.id}>
              {station.name}
            </Cell>
          ))}

          {!(metroStations.filter(c => {
            return String(c.name).toLocaleLowerCase().match(searchQuery)
          }).length) ? <Div>  </Div> : null}

        </List>
      </Group>
    );
  }


  renderFaculties () {
    const {faculties, searchQuery} = this.state;
    const {hashParams} = this.props
    return (
      <Group header={<Header mode="secondary" aside={hashParams.get('can_change_university') !== 'no' ? <Button className="btn-15" mode="tertiary" onClick={this.goBack}> </Button> : null}> </Header>}>
        <List>
          {faculties.filter(c => {
            return String(c.title).toLocaleLowerCase().match(searchQuery)
          }).map(faculty => (
            <Cell onClick={() => this.selectFaculty(faculty.id, faculty.title)} key={"faculty" + faculty.id}>
              {faculty.title}
            </Cell>
          ))}
          {!(faculties.filter(c => {
            return String(c.title).toLocaleLowerCase().match(searchQuery)
          }).length) ? <Div>  </Div> : null}
        </List>
      </Group>
    );
  }

  renderChairs () {
    const {chairs, searchQuery} = this.state;
    const {hashParams} = this.props
    return (
      <Group header={<Header mode="secondary" aside={hashParams.get('can_change_faculty') !== 'no' ? <Button className="btn-15" mode="tertiary" onClick={this.goBack}> </Button> : null}> </Header>}>
        <List>
          {chairs.filter(c => {
            return String(c.title).toLocaleLowerCase().match(searchQuery)
          }).map(chair => (
            <Cell onClick={() => this.selectChair(chair)} key={"chair" + chair.id}>
              {chair.title}
            </Cell>
          ))}
          {!(chairs.filter(c => {
            return String(c.title).toLocaleLowerCase().match(searchQuery)
          }).length) ? <Div>  </Div> : null}
        </List>
      </Group>
    );
  }

  renderPage = () => {
    const {
      countries, 
      cities,
      unversities,
      faculties,
      chairs,
      schools,
      regions,
      metroStations,

      countryName, 
      cityName,
      universityName,
      chairName,
      regionName,
      metroStationName,


      selectedCountryId,
      selectedCityId,
      selectedUniversityId,
      selectedFacultyId,
      selectedChairId,
      selectedSchoolId,
      selectedRegionId,
      selectedMetroStationId,

      loadedCountries,
      loadedCities,
      loadedUniversities,
      loadedFaculties,
      loadedChairs,
      loadedSchools,
      loadedRegions,
      loadedMetroStations,

      searchQuery

    } = this.state;

    const {hashParams, activeHash} = this.props;


    if (
      //   
      (
        (loadedCountries && !countries.length && hashParams.get('country_id') == null) || 
        ((hashParams.get('country_id') != null) && !countryName && loadedCountries && selectedCountryId && !countries.length)
      ) ||
      //   
      (
        (loadedCities && !cities.length && (hashParams.get('city_id') == null) && selectedCountryId) || 
        ((hashParams.get('city_id') != null) && !cityName && loadedCities && !cities.length)
      ) ||
      
      (
        (loadedUniversities && !unversities.length && hashParams.get('university_id') == null && ["selectSchool", "selectMetroStation"].indexOf(activeHash) === -1)
      ) ||
      (
        (loadedFaculties && !faculties.length && hashParams.get('faculty_id') == null)
      ) ||
      (
        (loadedChairs && !chairs.length)
      )

    ) {
      // 
        if (!selectedCountryId && !selectedCityId && !selectedUniversityId && !selectedFacultyId) {
          return (this.renderCountries())
        } else if (!selectedCityId && !selectedUniversityId && !selectedFacultyId) {
          if (activeHash === "selectRegion") {
            return this.renderRegions();
          } else {
            return this.renderCities();
          }
        } else if (!selectedUniversityId && !selectedFacultyId) {
          if (activeHash === "selectSchool") {
            return this.renderSchools()
          } else if (activeHash === "selectMetroStation") {
            return this.renderMetroStations()
          } else {
            return this.renderUniversities()
          }
        } else if (!selectedFacultyId) {
          return this.renderFaculties()
        } else {
          return this.renderChairs()
        }
    } else if (
      (!loadedCountries && (!selectedCountryId && !countries.length && !selectedCityId && !selectedRegionId && !selectedUniversityId && !selectedFacultyId)) ||
      
      (!loadedCities && (!selectedCityId && !cities.length) && selectedCountryId  && !selectedUniversityId && !selectedFacultyId && activeHash !== "selectRegion") ||
      (!loadedRegions && (!selectedRegionId && !regions.length) && selectedCountryId  && !selectedUniversityId && !selectedFacultyId && activeHash === "selectRegion") ||
      
      (!loadedUniversities && (!selectedUniversityId && !unversities.length) && selectedCityId && !selectedFacultyId && ["selectSchool", "selectMetroStation"].indexOf(activeHash) === -1) ||
      (!loadedFaculties && (!selectedFacultyId && !faculties.length) && selectedUniversityId) ||
      
      (!loadedSchools && (!selectedSchoolId && !schools.length) && selectedCityId && activeHash === "selectSchool") ||
      (!loadedMetroStations && (!selectedMetroStationId && !metroStations.length) && selectedCityId && activeHash === "selectMetroStation") ||

      (!loadedChairs && (!selectedChairId && !chairs.length) && selectedFacultyId)
    ) {
      //  
      return (
        <PanelSpinner size="l"/>
      );
    } else {
      //   
      if (!selectedCountryId && !selectedCityId && !selectedUniversityId && !selectedFacultyId) {
        return (this.renderCountries())
      } else if (
        (!selectedCityId && !selectedUniversityId && !selectedFacultyId && activeHash !== "selectRegion") ||
        (!selectedRegionId && !selectedUniversityId && !selectedFacultyId && activeHash === "selectRegion")
      ) {
        if (activeHash === "selectRegion") {
          return this.renderRegions();
        } else {
          return this.renderCities();
        }
      } else if (
        (!selectedUniversityId && !selectedFacultyId && activeHash !== "selectSchool") || 
        (!selectedSchoolId && !selectedFacultyId && activeHash === "selectSchool")
      ) {
        if (activeHash === "selectSchool") {
          return this.renderSchools()
        } else if (activeHash === "selectMetroStation") {
          return this.renderMetroStations();
        } else {
          return this.renderUniversities()
        }
      } else if (!selectedFacultyId) {
        return this.renderFaculties()
      } else if (!selectedChairId) {
        return this.renderChairs()
      }
    }

  }
  render () {
    
    const {
      countries, 
      cities,

      countryName, 
      cityName,

      selectedCountryId,
      selectedCityId,
      selectedUniversityId,
      selectedFacultyId,
      selectedChairId,

    } = this.state;

    const {hashParams} = this.props;

    let countryIdParams = hashParams.get('country_id');
    let searchKey = '';
    
    if (selectedCountryId && !selectedCityId && !selectedUniversityId && !selectedChairId) {
      searchKey = 'city'
    } else if (selectedCityId && !selectedUniversityId && !selectedChairId) {
      searchKey = 'university'
    } else if (selectedUniversityId && !selectedFacultyId) {
      searchKey = 'faculty'
    } else if (selectedFacultyId) {
      searchKey = 'chair'
    } else {
      searchKey = 'country'
    }

    return (
      <Panel id={this.props.id}>
        <StandardSearchHeader hashParams={hashParams} setSearch={true} key={searchKey} onChange={this.changeSearchQuery}/>
        
        {this.renderPage()}
        <FixedLayout vertical="bottom">
          {this.state.showScrollToTop ? <Button onClick={() => smoothScrollToTop()} className="scroll-to-top-button"><Icon28ArrowUpOutline/></Button> : null}
        </FixedLayout>
      </Panel>
    );
  }
} 