import React from 'react';

import {
  Panel,
  Button
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import StandardRequestAccess from '../components/standardRequestAccess';

import Icon24ScanViewfinderOutline from '@vkontakte/icons/dist/24/scan_viewfinder_outline';

import bridge from '@vkontakte/vk-bridge';

export default class ReadQRCodePanel extends React.Component {
  
  scanQR = () => {
    if (bridge.supports("VKWebAppOpenCodeReader")) {
      bridge.send("VKWebAppOpenCodeReader", {}).then(res => {
        this.props.sendResponse(res)
      }).catch(e => {
        this.props.sendError("QR   ", e)
      })
    } else {
      this.props.sendError(" QR  ", {
        error_code: "not_supports"
      })
    }
  }

  render () {
    const {id, hashParams} = this.props;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader hashParams={hashParams}/>
        <StandardRequestAccess 
          hashParams={hashParams} 
          requests="  QR"
          action={<Button size="xl" onClick={this.scanQR} before={<Icon24ScanViewfinderOutline/>}></Button>}
        />
      </Panel>
    );
  }
}