import React from 'react';

import {
  Panel,
  Div,
  Button,
  Card,
  CardGrid,
  Placeholder
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import Icon56HideOutline from '@vkontakte/icons/dist/56/hide_outline';

const renderAction = (type="") => {
  switch (type) {
    case 'selectCity':
      return ' ';
    default: 
      return ' ';
  }
}
export default ({onRequestToken, id, activeHash, hashParams}) => (
  <Panel id={id} centered>
    <StandardSearchHeader hashParams={hashParams}/>
    <Placeholder
      icon={<Icon56HideOutline/>}
      action={<Button size="xl" onClick={onRequestToken}></Button>}
    > {renderAction(activeHash)},         .</Placeholder>
  </Panel>
)