import React from 'react';

import {
  Panel
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import StandardRequestAccess from '../components/standardRequestAccess';

import bridge from '@vkontakte/vk-bridge';

export default class ShowStoryBoxPanel extends React.Component {
  

  showStoryBox = () => {
    let {hashParams} = this.props

    if (bridge.supports("VKWebAppShowStoryBox")) {
      let backgroundType = hashParams.get('background_type') || 'none';
     
      let url = hashParams.get('url')
      let blob = hashParams.get('blob')
      let locked = Number(hashParams.get('locked')) === 1
      let attachment = hashParams.get('attachment')
      let stickers = hashParams.getAll('stickers')

      let params = {}

      if (backgroundType) params.background_type = backgroundType
      if (url) params.url = url
      if (blob) params.blob = blob

      stickers.forEach((sticker, i) => {
        try {
          sticker = JSON.parse(decodeURIComponent(sticker))
        } catch (e) {
          sticker = {}
        }
        stickers[i] = sticker;
      })

      if (stickers.length) params.stickers = stickers

      if (attachment) {
        try {
          attachment = JSON.parse(attachment)
          params.attachment = attachment
        } catch (e) {
          params.attachment = {}
        }
      }


      params.locked = locked

      bridge.send("VKWebAppShowStoryBox", params).then(res => {
        this.props.sendResponse(res);
      }).catch(e => {
        this.props.sendError("   ", e)
      });

    } else {
      this.props.sendError("      ", {
        error_code: "not_supports"
      })
    }
  }

  render () {
    const {id, hashParams} = this.props;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader hashParams={hashParams}/>
        <StandardRequestAccess 
          onClick={this.showStoryBox} 
          hashParams={hashParams} 
          requests="   "
          actionText=""
        />
      </Panel>
    );
  }
}