import React from 'react';

import {
  CardGrid,
  Placeholder,
  Avatar,
  Button,
  Card,
  Spinner
} from '@vkontakte/vkui';

import Icon56LockOutline from '@vkontakte/icons/dist/56/lock_outline';

import './css/standardRequestAccess.css';

export default ({hashParams, requests, onClick, actionText, action, icon, nameField, photoField, content=null, hideButton, showSpinner=false}) => {
  
  if (!nameField) nameField = 'bot_name';
  if (!photoField) photoField = 'bot_photo';

  let botName = hashParams.get(nameField) || "-"
  let botPhoto = hashParams.get(photoField) || "";

  return (
  <CardGrid>
    <Card mode="tint" className="full-size-card"><Placeholder
      icon={botPhoto ? <Avatar size={82} src={botPhoto}/> : icon ? icon : <Icon56LockOutline/>}
      header={botName}
      action={action ? action : !hideButton ? <Button size="xl" before={showSpinner ? <Spinner size="small" className="white-spinner"/> : null} onClick={onClick}>{actionText ? actionText : ""}</Button> : null}
    >{requests}<div style={{textAlign: 'left', marginTop: 12}}>{content}</div></Placeholder></Card>
  </CardGrid>);
};