import React from 'react';

import {
  Panel,
  Button
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import StandardRequestAccess from '../components/standardRequestAccess';

import Icon24Download from '@vkontakte/icons/dist/24/download';

import bridge from '@vkontakte/vk-bridge';

export default class DownloadFilePanel extends React.Component {
  
  downloadFile = () => {
    let {hashParams} = this.props;
    if (!(window.isAndroid || window.isWEB)) {
      if (bridge.supports("VKWebAppDownloadFile")) {
        bridge.send("VKWebAppDownloadFile", {
          url: hashParams.get('file_url'),
          filename: hashParams.get('file_name')
        }).then(res => {
          this.props.sendResponse(res)
        }).catch(e => {
          this.props.sendError("   ", e)
        })
      } else {
        this.props.sendError("      ", {
          error_code: "not_supports"
        })
      }   
    }
  }

  render () {
    const {id, hashParams} = this.props;
    
    const canDownload = window.isAndroid || window.isWEB;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader hashParams={hashParams}/>
        <StandardRequestAccess 
          onClick={this.downloadFile} 
          hashParams={hashParams} 
          requests="  "
          action={
            <Button 
              before={<Icon24Download/>} 
              size="xl" 
              onClick={canDownload ? null : this.downloadFile} 
              href={canDownload ? hashParams.get('file_url') : null} 
              download={canDownload ? hashParams.get('file_name') : null}
              target="_blank"
            ></Button>}

        />
      </Panel>
    );
  }
}