import React from 'react';

import {
  Panel,
  FormLayout,
  Input,
  Textarea,
  Checkbox,
  Select,
  Button,
  Radio,
  FormLayoutGroup
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import StandardRequestAccess from '../components/standardRequestAccess';

import bridge from '@vkontakte/vk-bridge';

/*
  form {
    fields: [],
    action_text: ""
  }
*/
export default class FillFormPanel extends React.Component {

  constructor (props) {
    super(props);

    const {hashParams} = props

    let form = {}
    let formQuery = hashParams.get('form')
    let dataLink

    let formIsValid = false;
    if (Object.prototype.toString.call(formQuery) !== '[object Object]' && formQuery.length) {
      try {
        formQuery = JSON.parse(formQuery);
        formIsValid = true;
      } catch (e) {
        formIsValid = false;
      }
    } else {
      formIsValid = true;
    }

    if (!formIsValid) {
      return this.props.sendError("    JSON[]", {
        error_code: "invalid_format"
      })
    }

    let actionText = formQuery.action_text || "";

    if (formQuery.fields && Array.isArray(formQuery.fields)) {
      formQuery.fields.forEach((row, i) => {
        const {type, name, default_value, options, required} = row;
        // const [type, name="", defaultValue="", options={}, required=false] = row;
        formQuery.fields[i] = {
          defaultValue: default_value,
          value: default_value || null,
          required,
          options,
          name,
          type,
          isValid: null
        }
      });
    }

    this.state = {
      ...form,
      actionText,
      formQuery
    }
  }

  handleInput = (e, needSave=true) => {
      let value = e.currentTarget.value;
      

      if (e.currentTarget.type === 'checkbox') {
          value = e.currentTarget.checked;
      }


      const formQuery = {...this.state.formQuery}

      formQuery.fields.forEach((row, i) => {
        if (row.name === e.currentTarget.name) {
          formQuery.fields[i].value = value
          formQuery.fields[i].isValid = null
          formQuery.fields[i].bottomMessage = null

          if (row.options.match && ["select", "radio", "checkbox"].indexOf(row.type) === -1) {
            let regExpString = Array.isArray(row.options.match) ? row.options.match[0] : row.options.match
            let regExpFlags = Array.isArray(row.options.match) ? row.options.match[1] : ""

            let regExp = new RegExp(regExpString, regExpFlags);
            if (row.value.match(regExp)) {
              formQuery.fields[i].isValid = true;
            } else {
              formQuery.fields[i].isValid = false;
            }
          }
        }
      })

      this.setState({
          formQuery
      });
  };


  renderTextField (row) {
    return (
      <Input 
        name={row.name}
        defaultValue={row.defaultValue}
        placeholder={row.options.placeholder}
        maxLength={row.options.max_length}
        type={row.inputType || "text"}
        top={row.options.top ? row.options.top + (row.required ? " *" : "") : ""}
        onChange={this.handleInput}
        bottom={row.bottomMessage}
        status={row.isValid === null ? null : row.isValid ? 'valid' : 'error'}
      />
    )
  }

  renderSelectField (row) {
    return (
      <Select />
    )
  }

  renderTextAreaField (row) {
    return (
      <Textarea 
        name={row.name}
        defaultValue={row.defaultValue}
        placeholder={row.options.placeholder}
        maxLength={row.options.max_length}
        top={row.options.top ? row.options.top + (row.required ? " *" : "") : ""}
        onChange={this.handleInput}
        bottom={row.bottomMessage}
        status={row.isValid === null ? null : row.isValid ? 'valid' : 'error'}
      />
    )
  }


  renderCheckboxField (row) {
    return (
      <Checkbox 
        name={row.name}
        defaultChecked={row.defaultValue}
        onChange={this.handleInput}
        top={row.options.top ? row.options.top + (row.required ? " *" : "") : ""}
        bottom={row.bottomMessage}
      >
        {row.options.text}
      </Checkbox>
    )
  }

  renderRadioField (row) {
    return (
      row.options.items && Array.isArray(row.options.items) ? 
      <FormLayoutGroup top={row.options.top ? row.options.top + (row.required ? " *" : "") : ""} status={row.isValid === null ? null : row.isValid ? 'valid' : 'error'} bottom={row.bottomMessage}>{row.options.items.map(r => (
        <Radio
          name={row.name}
          defaultChecked={row.defaultValue === r.value}
          onChange={this.handleInput}
          description={r.description}
          value={r.value}
        >{r.text}</Radio>
      ))}</FormLayoutGroup> : null
    )
  }


  renderRow (row) {
    switch (row.type) {
      case 'text':
        return this.renderTextField(row)
      case 'select':
        return this.renderSelectField(row)
      case 'textarea':
        return this.renderTextAreaField(row)
      case 'checkbox':
        return this.renderCheckboxField(row)
      case 'radio':
        return this.renderRadioField(row)
      case 'password':
        return this.renderTextField({
          ...row,
          inputType: 'password'
        })
      default:
        return null
    }

  }
  
  formIsValid () {
    let formIsValid = true;

    let formQuery = {...this.state.formQuery};

    let rowsFormQuery = formQuery.fields || [];
    rowsFormQuery = [...rowsFormQuery]

    rowsFormQuery.forEach((row, i) => {
      if (row.required && (!row.value) && ["select"].indexOf(row.type) === -1) {
        formIsValid = false
        row.isValid = false;
        row.bottomMessage = '  '
      }

      if (row.value) {
        
        if (row.options.same_with) {
          let sameWithValue = rowsFormQuery.find(r => r.name === row.options.same_with)
          if (sameWithValue) {
            sameWithValue = sameWithValue.value
            if (sameWithValue !== row.value) {
              formIsValid = false;
              row.isValid = false;
              row.bottomMessage = '      ' + row.options.same_with
            }
          }
        }

        if (row.options.match && ["select", "radio", "checkbox"].indexOf(row.type) === -1) {
          let regExpString = Array.isArray(row.options.match) ? row.options.match[0] : row.options.match
          let regExpFlags = Array.isArray(row.options.match) ? row.options.match[1] : ""

          let regExp = new RegExp(regExpString, regExpFlags);

          if (row.value.match(regExp)) {
            row.isValid = true;
          } else {
            row.isValid = false;
            row.bottomMessage = '   '
          }
        }
      }

      rowsFormQuery[i] = {
        ...row
      }
    })


    this.setState({
      formQuery
    })

    return formIsValid;
  }

  submitForm = (e) => {
    e.preventDefault();
    if (this.formIsValid()) {
      let form = {}
      const {formQuery} = this.state;
      formQuery.fields.forEach(row => {
        form[row.name] = row.value
      });
      console.info('is valid', form)
      this.props.sendResponse(form)
    }
  }

  render () {
    const {formQuery, actionText} = this.state;
    const {id, hashParams} = this.props;
    let rowsFormQuery = formQuery.fields || [];
    return (
      <Panel id={id}>
        <StandardSearchHeader hashParams={hashParams}/>
        <FormLayout>
          {rowsFormQuery.map((r) => this.renderRow(r))}
          <Button size="xl" onClick={this.submitForm}>{actionText}</Button>
        </FormLayout>
      </Panel>
    );
  }
}