import React from 'react';

import {
  PanelHeader,
  Search,
  PanelHeaderButton
} from '@vkontakte/vkui';

import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';

import bridge from '@vkontakte/vk-bridge';

import {smoothScrollToTop} from '../services/functions';

import './css/standardSearchHeader.css';

export default ({ onChange, onClose, closeIcon, key, title, setSearch, hashParams, after}) => {

  const isDesktopWebPlatfrom = hashParams.get('vk_platform') === 'desktop_web';
  console.log(hashParams.get('vk_platform'))
  /*left={<PanelHeaderButton onClick={(e) => {
    if (onClose) {
      onClose(e)
    } else {

      bridge.send("VKWebAppClose", {
        "status": "success", 
        "payload": {}
      });

      window.history.back();
    }
  }}*/
  return (<><PanelHeader key={key}>
    {setSearch ? <Search onChange={onChange} className={!isDesktopWebPlatfrom ? "margins" : ""}/> : 
    <div onClick={() => {
      smoothScrollToTop();
    }}>
      {title ? title : hashParams.get("title") ? hashParams.get("title") : "ChatBot Tools"}
    </div>
  }
  </PanelHeader>{after}</>);
}
