import React from 'react';

import {
  Panel,
  CardGrid,
  Card,
  Placeholder,
  Button,
  Avatar
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import StandardRequestAccess from '../components/standardRequestAccess';

import bridge from '@vkontakte/vk-bridge';

export default class WallPostPanel extends React.Component {
  
  wallPost = () => {
    const {hashParams} = this.props;
    if (bridge.supports("VKWebAppShowWallPostBox")) {
      let data = {}

      if (hashParams.get('message')) {
        data.message = hashParams.get('message')
      }

      if (hashParams.get('friends_only')) {
        data.friends_only = Number(Number(hashParams.get('friends_only')) === 1)
      }

      if (hashParams.get('attachments')) {
        data.attachments = hashParams.get('attachments')
      }

      if (hashParams.get('signed')) {
        data.signed = Number(Number(hashParams.get('signed')) === 1)
      }
        
      if (hashParams.get('publish_date')) {
        data.publish_date = Number(hashParams.get('publish_date'))
      }

      if (hashParams.get('mark_as_ads')) {
        data.mark_as_ads = Number(Number(hashParams.get('mark_as_ads')) === 1)
      }

      if (hashParams.get('close_comments')) {
        data.close_comments = Number(Number(hashParams.get('close_comments')) === 1)
      }

      bridge.send("VKWebAppShowWallPostBox", data).then(res => {
        this.props.sendResponse(res)
      }).catch(e => {
        this.props.sendError("   ", e)
      });
    } else {
      this.props.sendError("      ", {
        error_code: "not_supports"
      })
    }
  }

  render () {
    const {id, hashParams} = this.props;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader hashParams={hashParams}/>
        <StandardRequestAccess 
          onClick={this.wallPost} 
          hashParams={hashParams}
          actionText=""
          requests="     "
        />
      </Panel>
    );
  }
}