import React from 'react';

import {
  Panel,
  PanelHeader,
  Search,
  PanelSpinner,
  Div,
  Group,
  Header,
  List,
  Cell,
  Link
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';

import bridge from '@vkontakte/vk-bridge';

export default class GetCitiesPanel extends React.Component {
  constructor (props) {
    super(props);
    
    this.state = {
      countries: [],
      cities: [],
      selectedCountryId: 0,
      loadedCountries: false,
      loadedCities: false,
      countryName: '',
      page: 'countries'
    }

    this.scrolls = {
      'countries': 0,
      'countries': 0
    }

  }

  componentDidMount () {
    let {hashParams} = this.props;
    if (hashParams.get('country_id')) {
      this.goToCities()
    } else if (!this.state.countries.length) {
      this.loadCountries();
    }

    window.addEventListener("scroll", this.saveScrolls, false);
  }

  saveScrolls = (e) => {
    this.scrolls[this.state.page] = window.pageYOffset;
  }

  componentWillUnmount () {
    window.removeEventListener("scroll", this.saveScrolls)
  }

  componentDidUpdate () {
    window.scrollTo(0, this.scrolls[this.state.page]);
  }

  async goToCities () {
    let {hashParams, vkApi} = this.props;
    let country = await  vkApi('database.getCountriesById', {
      country_ids: Number(hashParams.get('country_id'))
    });

    country = country[0];

    this.setState({
      selectedCountryId: country.id,
      countryName: country.title,
      loadedCountries: true,
      page: 'cities'
    }, () => {
      this.loadCities();
    })
  }

  async loadCountries (q="") {
    
    this.setState({
      loadedCountries: false
    });

    let countries = await this.props.vkApi('database.getCountries', {
      need_all: 1,
      q,
      count: 1000
    });

    this.setState({
      loadedCountries: true,
      countries: countries.items
    })
  }

  async loadCities (q="") {
    this.setState({
      loadedCities: false
    });

    let cities = await this.props.vkApi('database.getCities', {
      country_id: this.state.selectedCountryId,
      q
    })

    this.setState({
      loadedCities: true,
      cities: cities.items
    })
  }

  selectCountry = (countryId) => {
    const {activeHash} = this.props;
    if (activeHash === "selectCountry") {
      this.props.sendResponse({
        "country_id": countryId,
        "country_title": this.state.countries.find(c => c.id === countryId).title
      });
    } else {
      this.setState({
        selectedCountryId: countryId,
        page: 'cities'
      }, () => {
        this.loadCities();
      });
    } 
  }

  selectCity = async (city) => {
    let {hashParams} = this.props;
    
    this.props.sendResponse({
      "city_id": city.id,
      "city_title": city.title,
      "country_id": this.state.selectedCountryId,
      "country_title": hashParams.get('country_id') && this.state.countryName ? this.state.countryName : this.state.countries.find(c => c.id === this.state.selectedCountryId).title
    });

    this.clearCities();
  } 

  clearCities = () => {
    let newState = {
      cities: [],
      selectedCountryId: 0,
      countryName: ''
    }

    const {hashParams} = this.props;

    if (hashParams.get('country_id') && !this.state.countries.length) {
      this.loadCountries();
    }

    this.setState(newState)
  }

  renderCities () {
    const {cities, loadedCities} = this.state;
    const {hashParams} = this.props
    let canChangeCountry = hashParams.get('can_change_country') !== 'no';
    return (
      !cities.length && !loadedCities ? <PanelSpinner size="l"/> :
      !cities.length ? <Div>  </Div> :
      <div>
        <Group header={<Header mode="secondary" aside={canChangeCountry ? <Link onClick={this.clearCities}> </Link> : null}> </Header>}>
          <List>
            {cities.map(city => (
              <Cell onClick={() => this.selectCity(city)} key={"city" + city.id}>
                {city.title}
              </Cell>
            ))}
          </List>
        </Group>
      </div>
    )

  }

  changeSearchQuery = (e) => {
    let q = e.target.value;
    if (this.state.selectedCountryId) {
      this.loadCities(q);
    } else {
      this.setState({
        countryName: String(q).toLocaleLowerCase()
      })
    }
  }

  render () {
    const {countries, loadedCountries, countryName, selectedCountryId} = this.state;
    const {hashParams} = this.props;
    let countryIdParams = hashParams.get('country_id');

    return (
      <Panel id={this.props.id}>
        <StandardSearchHeader hashParams={hashParams} setSearch={true} key={selectedCountryId ? "country" : "city"} onChange={this.changeSearchQuery}/>
        {!countries.length && !loadedCountries || (countryIdParams && !loadedCountries) || !loadedCountries ? <PanelSpinner size="l"/> : 
        !countries.length && (countryIdParams && loadedCountries && !countryName) ? <Div>  </Div> : 
          !selectedCountryId ? <Group header={<Header mode="secondary"> </Header>}>
            <List>
              {countries.filter(c => {
                return String(c.title).toLocaleLowerCase().match(countryName)
              }).map(country => (
                <Cell onClick={() => this.selectCountry(country.id)} key={"country" + country.id}>
                  {country.title}
                </Cell>
              ))}
            </List>
          </Group> : this.renderCities()
        }
      </Panel>
    );
  }
} 