import React from 'react';

import {
  Panel,
  CardGrid,
  Card,
  Placeholder,
  Button,
  Avatar,
  Checkbox,
  Div
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';
import StandardRequestAccess from '../components/standardRequestAccess';

import bridge from '@vkontakte/vk-bridge';

import {getSplittedAndFilteredString} from '../services/functions';

import './css/getUserData.css';

/**
   :

  scope -   (),     .  .
   :
   email -   email'
   phone -    
   info -     
   all -     

  disabled - ,     
  : all,      , : email,phone,info
  use_token -          (1/0)
  info_fields -   ,   ,      (   users.get)
      ,      . :
    minimal -    bdate,city,country,verified,nickname
    photos -    photo_50,photo_100,photo_200_orig,photo_200,photo_400_orig,photo_max,photo_max_orig
    personal -    personal,interests,music,books,tx,movies,games,quotes,about,status,education,universities,schools

    minimal_and_photos -      minimal   photos
    minimal_and_personal -      minimal   personal
    all -       (  )

             .
*/

export default class GetUserDataPanel extends React.Component {
  
  constructor (props) {
    super(props)
    this.scope = getSplittedAndFilteredString(props.hashParams.get('scope') || "")
    
    if (props.hashParams.get('scope') === "all") this.scope = ['info', 'phone', 'email'];

    this.disabled = getSplittedAndFilteredString(props.hashParams.get('disabled') || "").filter((a) => a !== "all")
    this.disabledAll = props.hashParams.get('disabled') === "all";
    this.state = {
      scopeSelected: {
        phone: true,
        email: true,
        info: true
      },
      loading: false
    }
    
    this.useToken = Number(this.props.hashParams.get('use_token')) === 1;
    this.dataFields = this.props.hashParams.get('info_fields') || "photo_200";
    
    this.fieldsTypes = {
      minimal: "bdate,city,country,verified,nickname",
      personal: "personal,interests,music,books,tx,movies,games,quotes,about,status,education,universities,schools,career,military",
      photos: "photo_50,photo_100,photo_200_orig,photo_200,photo_400_orig,photo_max,photo_max_orig",
    }

    if (this.dataFields === "minimal") {
      this.dataFields = this.fieldsTypes.minimal
    } else if (this.dataFields === "minimal_and_photos") {
      this.dataFields = this.fieldsTypes.minimal + "," + this.fieldsTypes.photos;
    } else if (this.dataFields === "personal") {
      this.dataFields = this.fieldsTypes.personal
    } else if (this.dataFields === "minimal_and_personal") {
      this.dataFields = this.fieldsTypes.minimal + "," + this.fieldsTypes.personal
    } else if (this.dataFields === "photos") {
      this.dataFields = this.fieldsTypes.photos
    } else if (this.dataFields === "all") {
      this.dataFields = this.fieldsTypes.minimal + "," + this.fieldsTypes.photos + "," + this.fieldsTypes.personal
    }
  }

  getEmail = () => {
    if (bridge.supports("VKWebAppGetUserInfo")) {
      bridge.send("VKWebAppGetUserInfo", {}).then(res => {
        this.props.sendResponse(res)
      }).catch(e => {
        this.props.sendError("     ", e)
      });
    } else {
      this.props.sendError("   l     ", {
        error_code: "not_supports"
      })
    }
  }

  renderRequestsContent () {
    if (!this.scope.length) return (
      <Div className="align-center">
        <span className="description"> .  scope  </span>
      </Div>
    );

    return this.scope.map((permission, i) => {
      return <Checkbox key={"permission-" + i} onChange={(e) => {
        this.setState({
          scopeSelected: {
            ...this.state.scopeSelected,
            [permission]: e.target.checked
          }
        })
      }} disabled={this.disabledAll || this.disabled.indexOf(permission) !== -1} className="checkkox-mini" defaultChecked={true}>{this.getPermissionTitle(permission)}</Checkbox>
    });
  }

  getPermissionTitle (permission) {
    switch (permission) {
      case 'email':
        return "  email";
      case 'phone':
        return "   ";
      case 'info':
        return "   ";
      default:
        return null;
    }
  }

  getData = async () => {
    let data = {}
    
    for (let permission of this.scope) {
      if (this.state.scopeSelected[permission]) {
        data[permission] = await this.getDataField(permission);
      } else {
        data[permission] = {
          error: true,
          error_message: "   ",
          error_code: "user_canceled"
        }
      }
    }
    
    this.props.sendResponse(data)
  }

  async getDataField (permission) {
    switch (permission) {
      case 'email':
        return this.makeApiDataRequest("VKWebAppGetEmail", "   email").then(res => {
          
          if (res.email) {
            res = {
              email: res.email
            }
          }

          return res;
        });
      case 'phone':
        return this.makeApiDataRequest("VKWebAppGetPhoneNumber", "    ").then(res => {
          if (res.phone_number) {
            res = {
              phone_number: res.phone_number
            }
          }
          return res;
        });
      case 'info':

        if (this.useToken) {
          if (this.props.userToken) {
            return this.getUserInfoByToken(this.props.userToken);
          } else {
            let token = await this.props.onRequestToken();
            return this.getUserInfoByToken(token);
          }
        }

        return this.makeApiDataRequest("VKWebAppGetUserInfo", "     ");
      default:
        return null;
    }
  }

  async getUserInfoByToken (token="") {
    if (!token) return {error: true, error_code: "invalid_token"}
      console.log(this.dataFields)
    this.setState({
      loading: true
    });

    return this.props.vkApi("users.get", {
      access_token: token,
      fields: this.dataFields
    }).then(u => {
      return u[0];
    }).catch(e => {
      return {
        error: true,
        ...e
      }
    }).then(r => {
      
      this.setState({
        loading: false
      });

      return r;
    })
  }

  async makeApiDataRequest (apiMethod, apiErrorMessage="   ", options={}) {
    if (bridge.supports(apiMethod)) {
      return bridge.send(apiMethod, options).then(res => {
        return res;
      }).catch(e => {
        return {
          error: true,
          error_message: apiErrorMessage,
          ...e
        }
      });
    } else {
      return {
        error: true,
        error_message: "  ",
        error_code: "not_supports"
      }
    }
  }

  render () {
    const {id, hashParams} = this.props;

    return (
      <Panel id={id} centered>
        <StandardSearchHeader hashParams={hashParams}/>
        <StandardRequestAccess 
          onClick={this.getData} 
          hashParams={hashParams}
          content={this.renderRequestsContent()}
          requests="    "
          hideButton={!this.scope.length}
          showSpinner={this.state.loading}
        />
      </Panel>
    );
  }
}