import 'url-search-params-polyfill';

import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';

import {
	View,
	ScreenSpinner,
	PanelSpinner,
	Panel
} from '@vkontakte/vkui';


import '@vkontakte/vkui/dist/vkui.css';

import GetCitiesPanel from './panels/getCities';
import GettingAuthPanel from './panels/gettingAuth';
import NotFoundPanel from './panels/notFound';
import HelloPanel from './panels/hello';
import GetEmailPanel from './panels/getEmail';
import GetPhoneNumberPanel from './panels/getPhoneNumber';
import JoinGroupPanel from './panels/joinGroup';
import AllowMessagesFromGroupPanel from './panels/allowMessagesFromGroup';
import RouteDialogPanel from './panels/routeDialog';
import GetAppVersionPanel from './panels/getAppVersion';
import ReadQRCodePanel from './panels/readQRCode';
import DownloadFilePanel from './panels/downloadFile';
import FillFormPanel from './panels/fillForm';
import WallPostPanel from './panels/wallPost'
import GetChairsPanel from './panels/getChairs';
import ShowStoryBoxPanel from './panels/showStoryBox';
import RenderListPanel from './panels/renderList';
import SelectGeoPanel from './panels/selectGeo';
import GetUserInfoPanel from './panels/getUserInfo';
import GetUserDataPanel from './panels/getUserData';


import {fetch} from 'whatwg-fetch'

import './css/main.css';

export const APP_ID = 7388296;

class App extends React.Component {
	constructor (props) {
		super(props);

		this.queryString = window.location.search;
		
		this.lastTimeApiCallLastTick = 0;
		this.lastTimeApiCallFirstTick = 0;

		this.apiCallsCounter = 0;

		this.queryParams = new URLSearchParams(this.queryString);
		
		window.isWEB = this.queryParams.get('vk_platform') === 'desktop_web' || this.queryParams.get('vk_platform') === 'mobile_web';
		
		window.isAndroid = this.queryParams.get('vk_platform') === 'mobile_android' || this.queryParams.get('vk_platform') === 'mobile_android_messenger';
		window.isIOS = this.queryParams.get('vk_platform') === 'mobile_ios' || this.queryParams.get('vk_platform') === 'mobile_ios_messenger';

		this.supportsHashes = [
			'selectCity', 
			'selectCountry', 
			'getEmail', 
			'getPhoneNumber',
			'joinGroup',
			'allowMessagesFromGroup',
			'routeDialog',
			'getAppVersion',
			'readQRCode',
			'downloadFile',
			'fillForm',
			'wallPost',
			'selectChair',
			'selectFaculty',
			'selectUniversity',
			'showStoryBox',
			'selectSchool',
			'selectRegion',
			'selectMetroStation',
			'renderList',
			'selectGeo',
			'getUserInfo',
			'getUserData'
		];


		this.databaseMethods = ['selectCountry', 'selectCity', 'selectChair', 'selectCountry', 'selectFaculty', 'selectUniversity', 'selectRegion', 'selectSchool', 'selectMetroStation']
		this.apiCache = {}
		this.wasClosed = false;


		let hashParams = window.location.hash.replace(/^#/, '?');
		if (hashParams.replace('?', '').match(/^(\d+)/)) hashParams = `?method=routeDialog&bot_id=${hashParams.replace('?', '')}`;

		

		hashParams = new URLSearchParams(hashParams);

		this.hashParams = hashParams;


		this.state = {
			userToken: '',
			activePanel: 'loading',
			activeHash: '',
			popout: null,
			hashParams
		}

		bridge.subscribe((e) => {
			if (!e.detail) return;
			if (e.detail.type === "VKWebAppLocationChanged" || (e.detail.type === "VKWebAppViewRestore" && this.wasClosed)) {
				// window.location.reload();
			} else if (e.detail.type === "VKWebAppViewHide") {
				this.wasClosed = true;
			} else if (e.detail.type === "VKWebAppUpdateConfig") {
				document.body.setAttribute("scheme", e.detail.data.scheme)
			}
		})
 	}

 	componentWillMount () {
 		this.getAppOptions();
 	}

 	async getAppOptions () {
 		let options = this.hashParams;
 		let setOptions = new Map();
 		
 		setOptions.__proto__.getAll = function (e) {return setOptions.get(e)}


 		if (this.hashParams.get('launch_options')) {
 			options = await fetch(this.hashParams.get('launch_options')).catch(e => {
 				this.sendError("     ", {
 					...e,
 					error_code: "launch_options_unavailable"
 				})
 				throw e;
 			});

 			options = await options.text()

 			try {
 				options = JSON.parse(options)
 			} catch (e) {
 				this.sendError("   ", {
 					error_code: "launch_options_invalid_json"
 				})
 				return
 			}

 			if (!options.options || Object.prototype.toString.call(options) !== '[object Object]') {
 				this.sendError("   .   options{} ", {
 					error_code: "launch_options_invalid_format"
 				})
 			}

 			for (let i in options.options) {
 				setOptions.set(i, options.options[i])
 			}

 			setOptions.set('launch_options', this.hashParams.get('launch_options'))

 			options = setOptions
 		}

 		this.hashParams = options

 		let activeHash = options.get('method');

		if (this.supportsHashes.indexOf(activeHash) === -1) {
			if (activeHash && activeHash.length) {
				activeHash = 'notFound';
			} else {
				activeHash = 'hello';
			}
		}

		let activePanel = 'gettingAuth';
		let alreadyAuth = this.queryParams.get('vk_access_token_settings') === "status";

		if (alreadyAuth || (this.databaseMethods.indexOf(activeHash) === -1)) {
			activePanel = activeHash;
		}

		if (alreadyAuth) {
			this.requestToken();
		}

		this.setState({
			activeHash,
			activePanel,
			hashParams: options
		})

 	}

 	componentDidMount () {
 		if (this.state.activeHash === 'notFound') {
 			this.sendError("  ", {
 				"errror_code": "method_not_found"
 			}, false)
 		}
 	}

 	sendResponse = async (response={}, close=true) => {
 		
 		let appPayload = {
 			method: this.state.activeHash,
 			hash: window.location.hash.replace('#', ''),
 			...response
 		}

 		if (!appPayload.error) appPayload.success = true;

 		let botId = this.hashParams.get('bot_id');

    if (botId) {
      await bridge.send("VKWebAppSendPayload", {
        "group_id": Number(botId), 
        "payload": appPayload
      });
    }

    if (close) {
    	this.wasClosed = true;
    	if (this.queryParams.get('vk_platform') === 'desktop_web' || this.queryParams.get('vk_platform') === 'mobile_web') {
    		window.history.back();
    	} else {
    		bridge.send("VKWebAppSetLocation", {"location": window.location.hash + '&' + new Date().getTime()});
    		await bridge.send("VKWebAppClose", {
		      "status": "success", 
		      "payload": appPayload
		    });
    	}
    }
 	}

 	sendError = async (message="", response={}, close=true) => {
 		return this.sendResponse({
 			...response,
 			error: true,
 			error_message: message
 		}, close)
 	}

	requestToken = async () => {

		return bridge.send("VKWebAppGetAuthToken", {
			"app_id": APP_ID, 
			"scope": "status"
		}).then(token => {
			this.setState({
				userToken: token.access_token,
				activePanel: this.state.activePanel === 'gettingAuth' ? this.state.activeHash : this.state.activePanel
			});

			return token.access_token;
		});
	}

	async sleep (ts) {
		return new Promise((resolve, reject) => {
			setTimeout(() => resolve(true), ts)
		})
	}

	makeApiCall = async (method, params={}, cache=true) => {

		let requestbase64 = '';

		if (cache) {
			requestbase64 = btoa(JSON.stringify([method, params]))
			if (this.apiCache[requestbase64]) return this.apiCache[requestbase64];
		}

		if (this.apiCallsCounter % 2 === 0) {
			// Check limits
			if (this.lastTimeApiCallLastTick - this.lastTimeApiCallFirstTick <= 1000) {
				console.error('freeze api for ', 1000 - (this.lastTimeApiCallLastTick - this.lastTimeApiCallFirstTick))
				await this.sleep(1000 - (this.lastTimeApiCallLastTick - this.lastTimeApiCallFirstTick))
			}
		}

		const {userToken} = this.state;
		
		if (this.apiCallsCounter % 2 === 0) {
			this.lastTimeApiCallFirstTick = new Date().getTime()
		}

		this.apiCallsCounter += 1;
		this.lastTimeApiCallLastTick = new Date().getTime()

		return bridge.send("VKWebAppCallAPIMethod", {
      "method": method, 
      "request_id": String(new Date().getTime()), 
      "params": {
      	"v": "5.103",
      	"access_token": userToken,
      	...params
      }
    }).then(res => {
    	if (cache) this.apiCache[requestbase64] = res.response;
    	return res.response;
    }).catch(async e => {
    	if (e.error_data.error_reason.error_code === 6) {
    		console.error(e)
    		await this.sleep(500)
    		return this.makeApiCall(method, params)
    	} else {
    		throw e
    	}
    })
	}

	render () {
		let {activePanel} = this.state;
		const {popout, userToken, hashParams, activeHash} = this.state;
		
		if (activePanel !== "notFound" && activePanel !== "gettingAuth" && activePanel !== "hello" && !userToken
			&& this.databaseMethods.indexOf(activeHash) !== -1) {
			activePanel = "loading";
		}

		if (this.databaseMethods.indexOf(activePanel) !== -1) {
			activePanel = "selectChair"
		}

		if (activePanel === "loading") {
			return (
				<View key="loading" activePanel="loading" header={false}>
					<Panel id="loading" centered>
						<PanelSpinner size="xl"/>
					</Panel>
				</View>
			);
		}

		return (
			<View activePanel={activePanel} popout={popout} header={true}>
				<HelloPanel id="hello" hashParams={hashParams}/>
				<NotFoundPanel id="notFound" hashParams={hashParams}/>

				<RenderListPanel id="renderList" activeHash={this.state.activeHash} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>


				<GetEmailPanel id="getEmail" activeHash={this.state.activeHash} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<GetUserInfoPanel id="getUserInfo" activeHash={this.state.activeHash} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<GetUserDataPanel id="getUserData" onRequestToken={this.requestToken} activeHash={this.state.activeHash} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				
				<WallPostPanel id="wallPost" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				
				<ReadQRCodePanel id="readQRCode" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<DownloadFilePanel id="downloadFile" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				
				<RouteDialogPanel id="routeDialog" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<FillFormPanel id="fillForm" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				
				<ShowStoryBoxPanel id="showStoryBox" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				
				<JoinGroupPanel id="joinGroup" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<GetAppVersionPanel id="getAppVersion" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<AllowMessagesFromGroupPanel id="allowMessagesFromGroup" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<GetPhoneNumberPanel id="getPhoneNumber" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} vkApi={this.makeApiCall} hashParams={hashParams}/>
				
				<GetCitiesPanel id="selectCity" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} userToken={userToken} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<GetChairsPanel id="selectChair" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} userToken={userToken} vkApi={this.makeApiCall} hashParams={hashParams}/>
				<SelectGeoPanel id="selectGeo" activeHash={this.state.activeHash} sendError={this.sendError} sendResponse={this.sendResponse} userToken={userToken} vkApi={this.makeApiCall} hashParams={hashParams}/>
				
				<GettingAuthPanel hashParams={hashParams} id="gettingAuth" activeHash={this.state.activeHash} sendError={this.sendError} onRequestToken={this.requestToken}/>
			</View>
		);
	}
}

export default App;

