import React from 'react';

import {
  Panel,
  List,
  Cell,
  Avatar,
  Spinner,
  Search,
  Card,
  CardGrid,
  Button,
  Gallery,
  Div,
  Footer,
  Checkbox,
  FixedLayout,
  Counter,
  Link
} from '@vkontakte/vkui';

import Flickity from 'react-flickity-component';

import Icon28MarketOutline from '@vkontakte/icons/dist/28/market_outline';
import Icon28ChecksOutline from '@vkontakte/icons/dist/28/checks_outline';

import Icon24DoneOutline from '@vkontakte/icons/dist/24/done_outline';
import Icon24MarketOutline from '@vkontakte/icons/dist/24/market_outline';

import {fetch} from 'whatwg-fetch'
import {smoothScrollToTop} from '../services/functions';

import classnames from 'classnames';

import StandardSearchHeader from '../components/standardSearchHeader';

import bridge from '@vkontakte/vk-bridge';
import InfiniteScroll from 'react-infinite-scroller';
import { Grid, Row, Col } from 'react-flexbox-grid';



import './css/renderList.css';

// class Flickity {}
export default class RenderListPanel extends React.Component {
  
  constructor (props) {
    super(props)

    const {hashParams} = props;

    let list = hashParams.get('list');
    if (!list.options) list.options = {}


    let items = list.items;

    this.list = list
    
    this.itemsGalleries = {}
    this.state = {
      items,
      hasMore: list.options.load_more_url ? true : false,
      nextPageUrl: this.list.options.load_more_url || "",
      enableSearch: Boolean(this.list.options.enable_search),
      searchQuery: "",
      cardSize: 0,
      windowWidth: window.innerWidth,
      selectedItemIds: []
    }

    this.loading = false;
  }

  loadMore = async () => {
    if (this.state.nextPageUrl) {
      if (!this.loading) {
        return this.fetchMore()
      }
    } else {
      this.setState({
        hasMore: false
      })
      return true;
    }
  }

  fetchMore (q="", refresh=false) {

    this.loading = true;
    const {hashParams} = this.props;


    let nextPageUrl = q && refresh ? hashParams.get('launch_options') : this.state.nextPageUrl;
    let {searchQuery} = this.state;
    
    let url = new URL(nextPageUrl),
    
    params = {
      q: q ? q : searchQuery
    }
    
    Object.keys(params).forEach(key => url.searchParams.append(key, params[key]))
    


    return fetch(url).then(async (res) => {
      res = await res.json();
      let items = q && refresh ? (res.list.items || []) : (res.items || []);

      let hasMore = q && refresh ? true : Boolean(res.has_more);
      let nextPageUrl = q && refresh ? res.list.options.load_more_url : (res.next_page_url || "");

      this.setState({
        items: [...this.state.items, ...items],
        hasMore,
        nextPageUrl
      })

      this.loading = false;
    }).catch(e => {
      this.setState({
        hasMore: false
      })
      this.loading = false;
    })
  }

  makeSearch = (event) => {
    if (!window.scrolling) {
      smoothScrollToTop();
    }
    if (this.list.options.enable_server_search) {
      // Make new fetch for refresh
      this.fetchMore(event.target.value, true)
    }
    this.setState({
      searchQuery: event.target.value
    });
  }

  componentDidMount () {
    let card = document.querySelector('.card');
    if (card) {
      this.setState({
        cardSize: card.offsetWidth
      })
    }

    window.onresize = () => {
      let card = document.querySelector('.card');
      let windowWidth = window.innerWidth;
      
      if (windowWidth <= 400 && this.state.windowWidth > 400 || windowWidth > 400 && this.state.windowWidth <= 400) {
        this.updateCardWidth = true;
      }

      if (card) {
        this.setState({
          cardSize: card.offsetWidth,
          windowWidth
        })
      }
    }
  }

  componentDidUpdate () {
    if (this.updateCardWidth) {
      let card = document.querySelector('.card');
      this.updateCardWidth = false;
      if (card) {
        this.setState({
          cardSize: card.offsetWidth
        })
      }
    }
  }

  componentWillUnmount () {
    window.onresize = null;
  }

  renderIcon (icon="") {
    switch (icon) {
      case 'market':
        return <Icon28MarketOutline/>
      default:
        return null;
    }
  }

  renderActionIcon (icon="") {
    switch (icon) {
      case 'done':
        return <Icon24DoneOutline/>
      case 'market':
        return <Icon24MarketOutline/>
      default:
        return null;
    }
  }

  sendClick (itemId=0, payload="") {
    return this.sendResponse({
      "button_payload": payload,
      "selected_item_id": itemId
    })
  }

  sendResponse (options={}) {
    return this.props.sendResponse({
      ...options,
      "list_type": this.list.type,
      "list_name": this.list.name || ""
    })
  }

  sendSelectedItems = () => {
    return this.sendResponse({
      "selected_item_ids": this.state.selectedItemIds
    })
  }

  renderButtons (item={}, k) {
    let buttons = item.buttons || [];
    if (!buttons.length) buttons = this.list.options.buttons || [];
    const {windowWidth} = this.state;
    return (
      <div className="buttons">
        {buttons.map((button, i) => {
          
          let key = "button_" + i + "_" + k;
          let size = windowWidth > 400 && windowWidth < 600 ? "m" : "xl";
          let isSelectedItem = this.isSelectedItem(item);
          

          if (button.action && button.action.type) {
            
            let selectedStateColor = button.action.selected_state && button.action.selected_state.color ? button.action.selected_state.color : button.color;
            let selectedStateLabel = button.action.selected_state && button.action.selected_state.label ? button.action.selected_state.label : button.action.label;
            let selectedStateIcon = button.action.selected_state && button.action.selected_state.icon ? button.action.selected_state.icon : button.icon;

            switch (button.action.type) {
              case 'text':
                return (<div className="button-wrap" key={key}>
                      <Button before={this.renderActionIcon(button.icon)} size={size} onClick={() => button.action.payload ? this.sendClick(item.id, button.action.payload) : null} stretched mode={button.color || "primary"}>
                        {button.action.label}
                      </Button>
                  </div>);
              case 'open_link':
                return (
                  <div className="button-wrap" key={key}>
                    <Link rel="noreferrer noopener" href={button.action.link || ""} target="_blank">
                      <Button before={this.renderActionIcon(button.icon)} size={size} stretched mode={button.color || "primary"}>
                        {button.action.label}
                      </Button>
                    </Link>
                  </div>
                );
              case 'select':
                if (this.list.options.enable_select) {
                  return (
                    <div className="button-wrap" key={key}>
                      <Button before={isSelectedItem ? selectedStateIcon ? this.renderActionIcon(selectedStateIcon) : this.renderActionIcon(button.icon) : this.renderActionIcon(button.icon)} onClick={() => this.selectItem(item)} size={size} stretched mode={isSelectedItem ? selectedStateColor : button.color || "primary"}>
                        {isSelectedItem ? selectedStateLabel : button.action.label}
                      </Button>
                    </div>
                  );
                } else {
                  return null;
                }
            }
          } else {
            return null;
          }
        })}
      </div>
      );
  }

  getButtons (item) {
    if (item.buttons && item.buttons.length) return item.buttons;
    return this.list.options.buttons || [];
  }

  selectItem = (item={}) => {
    if (item.id) {
      let selectedItemIds = [...this.state.selectedItemIds]
      if (this.isSelectedItem(item)) {
        selectedItemIds.splice(selectedItemIds.indexOf(item.id), 1)
      } else {
        selectedItemIds.push(item.id)
      }

      this.setState({selectedItemIds})
    }
  }

  isSelectedItem (item={}) {
    if (item.id) {
      return this.state.selectedItemIds.indexOf(item.id) !== -1;
    } else {
      return false;
    }
  }


  render () {
    const {id, hashParams} = this.props;
    let {items, hasMore} = this.state;
    let searchQuery = this.state.searchQuery.toLocaleLowerCase()

    const spinnerStyle = {
      marginTop: 24
    }

    if (!this.list.options.enable_server_search) {
      let itemsMatchByTitle = items.filter((item, i)=> {
        if (this.list.options.enable_server_search) return true;
        item.__key = i;
        return String(item.title).toLocaleLowerCase().indexOf(searchQuery) !== -1
      })

      let itemsMatchByDescription = items.filter((item, i)=> {
        item.__key = i;
        return String(item.description).toLocaleLowerCase().indexOf(searchQuery) !== -1
      })

      items = [...itemsMatchByTitle];
      itemsMatchByDescription.forEach((item) => {
        if (!itemsMatchByTitle.find(i => i.__key === item.__key)) {
          items.push(item)
        }
      })
    }

    let listType = this.list.type;
    let list = null;
    let {cardSize, windowWidth, selectedItemIds} = this.state;
    
    let ratio = this.list.options.aspect_ratio ? this.list.options.aspect_ratio.split(":") : "";
    if (!ratio.length) ratio = [13, 8];

    let galleryHeight = (cardSize * ratio[1]) / ratio[0] || "auto";
    const galleryStyles = {height: galleryHeight}

    // items = [];

    if (items.length) {
      switch (listType) {
        case 'vk_entities':
          list = items.map((item, i) => (
            <Cell 
              before={item.avatar_src ? <Avatar size={this.list.options.avatar_size || 48} src={item.avatar_src}/> : null}
              description={item.description}
              multiline={this.list.options.multiline}
              href={item.link}
              target="_blank"
              rel="noreferrer noopener"
              key={"cell-item" + i}
            >{item.title}</Cell>
          ));
          break;
        case 'cards':
          let items_ = [];

          items.forEach((el, i) => {
            let arr = [];
            if ((i + 1) % 2 === 1) {
              arr.push(items[i]);
              if (items[i + 1]) {
                arr.push(items[i + 1]) 
              } else {
                arr.push(null) 
              }
            }

            if (arr.length) {
              items_.push(arr);
            }
          });
          
          let cardSize = windowWidth > 400 ? "m" : "l";
          list = (
            <Grid fluid>
              <Row>
              {items.map((item, k) => {
                if (item) {
                  
                  let haveSelectButton = this.getButtons(item).find(b => b.action && b.action.type === "select");
                  const flckityOptions = {
                    initialIndex: this.itemsGalleries[item.id] || 0,
                    pageDots: false,
                    prevNextButtons: false,
                    fullscreen: true,
                    adaptiveHeight: true,
                    wrapAround: true
                  }

                  return (
                    <Col xs={12} md={3} key={"card" + k + "_" + item.id}><Card mode="shadow" className="card" size={cardSize}>
                    {item.images && Array.isArray(item.images) ?
                      <div>
                      {this.list.options.enable_select && !haveSelectButton ? <Checkbox defaultChecked={this.isSelectedItem(item)} onChange={() => this.selectItem(item)} className="card--checkbox"/> : null}
                      <Flickity flickityRef={c => {
                        c.on('settle', () => this.itemsGalleries[item.id] = c.selectedIndex)
                      }} options={flckityOptions}
                        onClick={() => bridge.send("VKWebAppShowImages", {
                          "images": item.images,
                          "start_index": this.itemsGalleries[item.id] || 0,
                       })}
                      >
                         {item.images.map(src => (<div className="slide-wrap"><img style={galleryStyles} src={src} alt=""/></div>))}
                      </Flickity>
                      </div>
                     : null}
                     <Div className="card--footer">
                      <div className="card--footer-title">{item.title ? <b>{item.title}</b> : null}
                      {item.description ? <span className="card--description">
                        <span className="description">{item.description}</span>
                      </span> : null}
                      </div>
                      {this.renderButtons(item, k + "_")}
                    </Div>
                    </Card></Col>
                  );
                } else {
                  return (<div className="card"></div>);
                }
              })}
              </Row>
              </Grid>
          );


          // list = items_.map((row, i) => (
          //   <div key={"row" + i}><CardGrid className="cards-wrapper">
          //     {row.map((item, k) => {
          //       if (item) {
                  
          //         let haveSelectButton = this.getButtons(item).find(b => b.action && b.action.type === "select");
          //         const flckityOptions = {
          //           initialIndex: this.itemsGalleries[item.id] || 0,
          //           pageDots: false,
          //           prevNextButtons: false,
          //           fullscreen: true,
          //           adaptiveHeight: true,
          //           wrapAround: true
          //         }

          //         return (
          //           <Card key={"card" + i + "_" + item.id} mode="shadow" className="card" size={cardSize}>
          //           {item.images && Array.isArray(item.images) ?
          //             <div>
          //             {this.list.options.enable_select && !haveSelectButton ? <Checkbox defaultChecked={this.isSelectedItem(item)} onChange={() => this.selectItem(item)} className="card--checkbox"/> : null}
          //             <Flickity flickityRef={c => {
          //               c.on('settle', () => this.itemsGalleries[item.id] = c.selectedIndex)
          //             }} options={flckityOptions}
          //               onClick={() => bridge.send("VKWebAppShowImages", {
          //                 "images": item.images,
          //                 "start_index": this.itemsGalleries[item.id] || 0,
          //              })}
          //             >
          //                {item.images.map(src => (<img style={galleryStyles} src={src} alt=""/>))}
          //             </Flickity>
          //             </div>
          //            : null}
          //            <Div className="card--footer">
          //             <div className="card--footer-title">{item.title ? <b>{item.title}</b> : null}
          //             {item.description ? <span className="card--description">
          //               <span className="description">{item.description}</span>
          //             </span> : null}
          //             </div>
          //             {this.renderButtons(item, k + "_" + i)}
          //           </Div>
          //           </Card>
          //         );
          //       } else {
          //         return (<div className="card"></div>);
          //       }
          //     })}
          //   </CardGrid></div>
          // ))
          break;
      }
    } else {
      list = (<Footer>{searchQuery ? (this.list.options.empty_search_error_text ? this.list.options.empty_search_error_text : "   ") :  this.list.options.empty_error_text ? this.list.options.empty_error_text : " "}</Footer>)
    }

    return (  
      <Panel id={id}>
        <StandardSearchHeader hashParams={hashParams} after={this.state.enableSearch ? <FixedLayout vertical="top"><Search onChange={this.makeSearch}/></FixedLayout> : null}/>
        <InfiniteScroll
          pageStart={0}
          hasMore={hasMore}
          loader={<div style={spinnerStyle}><Spinner/></div>}
          loadMore={this.loadMore}
          threshold={10}
        >
          <div className={classnames("content-page", {
            "search": this.state.enableSearch
          })}>
            {list}
          </div>
        </InfiniteScroll>
        {this.list.options.enable_select ? <FixedLayout vertical="bottom">
            <div className="bottom-list-actions">
              <div className="bottom-list-actions--items">
                <Div>
                  <Button size="l" stretched after={<Counter>{selectedItemIds.length}</Counter>} mode="secondary"><Icon28ChecksOutline/></Button>
                </Div>
                <Div className="primary-action">
                  <Button disabled={selectedItemIds.length === 0} size="l" onClick={this.sendSelectedItems} stretched before={this.renderIcon(this.list.options.complete_action_icon)}>{this.list.options.complete_action_text ? this.list.options.complete_action_text : ""}</Button>
                </Div>
              </div>
            </div>
          </FixedLayout> : null}
      </Panel>
    );
  }
}