import React from 'react';

import {
  Panel,
  PanelHeader,
  Search
} from '@vkontakte/vkui';

import StandardSearchHeader from '../components/standardSearchHeader';

import './css/selectGeo.css';

export default class SelectGeoPanel extends React.Component {
  
  componentDidMount () {
    window.DG.then(function () {
        window.DG.map('map', {
            center: [54.98, 82.89],
            zoom: 13
        });
    });
  }

  render () {
    const {hashParams} = this.props;
    return (
      <Panel id={this.props.id}>
        <StandardSearchHeader hashParams={hashParams}/>
        <div id="map" className="two-gis-map-container"></div>
      </Panel>
    );
  }
} 